import { create } from "zustand";
import Cookies from "js-cookie";
const useAuthStore = create((set, get) => ({
    isAuthenticated: !!Cookies.get("accessToken"),
    userId: "",
    profileImg: "",
    login: () => set((state) => (Object.assign(Object.assign({}, state), { isAuthenticated: true }))),
    logout: () => set((state) => (Object.assign(Object.assign({}, state), { isAuthenticated: false }))),
}));
export default useAuthStore;
